import { reactive, readonly } from 'vue';

/**
 * @description 用户状态
 * @type {Object}
 * @property {Object|null} userInfo - 用户信息
 * @property {boolean} isLoggedIn - 是否已登录
 * @property {boolean} isLoading - 是否正在加载
 * @property {Object|null} error - 错误信息
 */
const state = reactive({
  userInfo: null,
  isLoggedIn: false,
  isLoading: false,
  error: null
});

/**
 * @description 导出只读状态，防止在组件中直接修改状态
 */
export const useUserState = () => readonly(state);

/**
 * @description 导出内部状态，供actions使用
 */
export const internalState = state;